/* General body styles */
body {
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f9;
  margin: 0;
  padding: 0;
}

/* Container for the entire app */
.app-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

/* Navbar styles */
.navbar {
  background-color: #3f51b5;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
}

.navbar a {
  color: white;
  text-decoration: none;
  padding: 10px;
  font-size: 18px;
}

.navbar a:hover {
  background-color: #283593;
  border-radius: 4px;
}

/* Buttons */
.button {
  background-color: #3f51b5;
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
}

.button:hover {
  background-color: #283593;
}

/* Form styles */
.form-container {
  background-color: #fff;
  padding: 20px;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  max-width: 500px;
  margin: 20px auto;
}

.form-container h2 {
  text-align: center;
  color: #3f51b5;
}

.form-container input[type="text"],
.form-container input[type="password"],
.form-container input[type="email"],
.form-container select {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.form-container input[type="submit"] {
  width: 100%;
  background-color: #3f51b5;
  color: white;
  border: none;
  padding: 10px;
  font-size: 18px;
  border-radius: 5px;
  cursor: pointer;
}

.form-container input[type="submit"]:hover {
  background-color: #283593;
}

/* Dashboard styles */
.dashboard {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.dashboard .card {
  background-color: #fff;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  padding: 20px;
  text-align: center;
  width: 30%;
}

.dashboard .card h3 {
  color: #3f51b5;
}

.dashboard .card p {
  font-size: 14px;
  color: #666;
}

/* Course list */
.course-list {
  background-color: #fff;
  padding: 20px;
  margin-top: 20px;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

.course-list h2 {
  color: #3f51b5;
}

.course-list ul {
  list-style-type: none;
  padding: 0;
}

.course-list ul li {
  background-color: #f4f4f9;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
}

.course-list ul li button {
  background-color: #3f51b5;
  color: white;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  border-radius: 3px;
}

.course-list ul li button:hover {
  background-color: #283593;
}

/* Footer styles */
.footer {
  background-color: #3f51b5;
  color: white;
  text-align: center;
  padding: 10px;
  position: fixed;
  bottom: 0;
  width: 100%;
}
